import React from 'react';
import { Link } from 'react-router-dom';

/**
 * @param {string[]} inputs
 * @param {string[]} fields
 */
const validateForm = (inputs, fields) => {
  let result = true;

  for (const field of fields) {
    if (!(inputs[field] && inputs[field].length)) {
      result = false;
      break;
    }
  }

  return result;
};

const View = ({ inputs, handleChange, handleSubmit, working, error }) => {
  const formValid = validateForm(inputs, [
    'email',
    'first_name',
    'last_name',
    'password'
  ]);

  return (
    <div className="register">
      <form onSubmit={handleSubmit}>
        <h1>Inscription</h1>
        {error ? <p>{error}</p> : <></>}
        <div className="groups">
          <div className="group">
            <label htmlFor="first_name">Prénom</label>

            <input
              autoFocus
              className="control"
              type="text"
              id="first_name"
              name="first_name"
              onChange={handleChange}
            />
          </div>

          <div className="group">
            <label htmlFor="last_name">Nom</label>

            <input
              autoFocus
              className="control"
              type="text"
              id="last_name"
              name="last_name"
              onChange={handleChange}
            />
          </div>

          <div className="group">
            <label htmlFor="email">Email</label>

            <input
              autoFocus
              className="control"
              type="text"
              id="email"
              name="email"
              onChange={handleChange}
            />
          </div>

          <div className="group">
            <label htmlFor="password">Mot de passe</label>

            <input
              className="control"
              type="password"
              id="password"
              name="password"
              onChange={handleChange}
            />
          </div>
        </div>
        <input
          className="btn"
          type="submit"
          value="Inscription"
          disabled={!formValid || working}
        />
        <p>
          Vous avez déjà un compte ? <Link to="/login">Connectez-vous ici</Link>
        </p>
      </form>
    </div>
  );
};

export default View;
